import http from './http'


export async function getWorkInfo(category) {
    return await http.request({
        url: `/work/info?category=${category}`,
        method: 'get'
    });
}


export async function getWorkDetail(category, user_id) {
    return await http.request({
        url: `/work/detail/${category}?user_id=${user_id}`,
        method: 'get'
    });
}


export async function workCollection(data) {
    return await http.request({
        url: `/work/collection`,
        method: 'post',
        data
    });
}


export async function workCollectionCancel(data) {
    return await http.request({
        url: `/work/collection`,
        method: 'delete',
        data
    });
}


export async function getCollection(user_id) {
    return await http.request({
        url: `/work/collection?user_id=${user_id}`,
        method: 'get'
    });
}


export async function updateWork(data) {
    return await http.request({
        url: `/work?work_id=${data.id}`,
        method: 'put',
        data
    });
}


export async  function getTopic() {
    return await http.request({
        url: `/topic`,
        method: 'get'
    })
}


export async  function createTopic(data) {
    return await http.request({
        url: `/topic`,
        method: 'post',
        data
    })
}
