<template>
    <div>
        <el-dialog
                :title="detailData.user_name"
                :visible.sync="show">
            <div>
                <el-avatar :size="50" :src="detailData.avatar">
                    <img src="https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png"/>
                </el-avatar>
                <el-image :src="detailData.url"></el-image>
                <p>简介: </p>
                <p v-if="!edit">{{ detailData.describe}}</p>
                <el-input
                        type="textarea"
                        :rows="2"
                        placeholder="请输入内容"
                        v-model="detailData.describe"
                        v-if="edit"
                >
                </el-input>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="collectionWork(collection?detailData.work_id:detailData.id, showBtn)"
               v-show="!showBtn && collection !== 3">收 藏</el-button>
    <el-button @click="collectionWork(collection?detailData.work_id:detailData.id, showBtn)"
               v-show="showBtn && collection !== 3">取消收藏</el-button>
                <el-button @click="edit=false" v-if="edit">取 消</el-button>
    <el-button v-show="collection === 3" @click="edit=true" v-if="!edit">编辑</el-button>
    <el-button type="primary" @click="hideDialog" v-if="!edit">确 定</el-button>
                <el-button type="primary" @click="save" v-if="edit">保 存</el-button>
        </span>
        </el-dialog>

    </div>
</template>

<script>
    import {megSuc} from "../../utils/message";
    import { workCollection, workCollectionCancel, getWorkDetail, getCollection, updateWork } from '@/service/info-api'
    export default {
        name: "DetailInfo",
        props: ['dialogTableVisible', 'detailData', 'detailList', 'collection'],
        created() {
            console.log(this.detailData)
        },
        watch:{
          show: function () {
              this.hideDialog()
          }
        },
        data(){
            return {
                show: this.dialogTableVisible,
                src: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
                showBtn: this.detailData.collection,
                userId: this.$store.getters.consumerInfo.id,
                secondCategory: this.$route.params.id,
                edit: false,
            }
        },
        methods: {
            hideDialog() {
                this.$emit('update:dialogTableVisible', false)
            },
            collectionWork(work_id, is_collection){
                let request_data = {
                    user_id: this.userId,
                    work_id: work_id
                }
                if (this.collection === 2){
                    workCollectionCancel(request_data)
                        .then((res) => {
                            if (res.code === 2000){
                                megSuc('取消收藏成功')
                                this.showBtn = !this.showBtn
                                getCollection(this.secondCategory, this.userId)
                                    .then((res) => {
                                        if (res.code === 2000){
                                            this.$emit('update:detailList', res.collection_list)
                                            this.$emit('update:dialogTableVisible', false)
                                        }
                                    })
                            }
                        })
                } else if (this.collection === 1){
                    if (!is_collection){
                        workCollection(request_data)
                            .then((res) => {
                                if (res.code === 2000){
                                    megSuc('收藏成功')
                                    this.showBtn = !this.showBtn
                                    getWorkDetail(this.secondCategory, this.userId)
                                        .then((res) => {
                                            if (res.code === 2000){
                                                this.$emit('update:detailList', res.detail_list)
                                            }
                                        })
                                }
                            })
                    } else {
                        workCollectionCancel(request_data)
                            .then((res) => {
                                if (res.code === 2000){
                                    megSuc('取消收藏成功')
                                    this.showBtn = !this.showBtn
                                    getWorkDetail(this.secondCategory, this.userId)
                                        .then((res) => {
                                            if (res.code === 2000){
                                                this.$emit('update:detailList', res.detail_list)
                                            }
                                        })
                                }
                            })
                    }

                }


            },
            save(){
                updateWork(this.detailData)
                .then((res) => {
                    if (res === 2000){
                        megSuc('修改成功')
                    }
                })

            }
        }
    }
</script>

<style scoped>

</style>
